import { Injectable } from '@angular/core';
import { of } from 'rxjs';
import { Observable } from 'rxjs';
import {WorkOrders} from "./data";
import {Detail} from "./Detail";
import {WorkOrder}  from "./WorkOrder";

@Injectable({
  providedIn: 'root'
})
export class WorkOrderService {

  constructor() { }

  getOrders(): Observable<WorkOrder[]>{
    return of(WorkOrders);
  }

  getOrder(id: number): Observable<WorkOrder | undefined>{
    return of(WorkOrders.find(Order => Order.Id == id ));
  }

  addOrder(order: WorkOrder): void{
    WorkOrders.push(order);
  }

  addDetail(id:number,Detail: Detail):void{
    WorkOrders.find(Order => Order.Id == id )?.Details.push(Detail);
  }
  
  
}
